body { font-family: "Noto Sans KR", sans-serif; }

#content { display: block; margin-left: auto; margin-right: auto; padding: 30px; }

.section { font-size: 1.4rem; }

.pop-wrapper { position: absolute; top: 45px; left: 50%; transform: translateX(-50%); }

.table-ty { border-top: 2px solid #222; }

.table-row { border-bottom: 1px solid #f1f1f1; }

.table-col { display: table; width: 100%; height: auto; min-height: 3.2rem; text-align: center; }

.table-col .lf { text-align: left; }

.table-col .ri { text-align: right; }

.table-col li { display: table-cell; padding: 10px; box-sizing: border-box; line-height: 1.6; vertical-align: middle; word-wrap: break-word; word-break: break-all; }

.table-col li.col1 { width: 15%; }

.table-col li.col2 { width: 15%; }

.table-col li.col3 { width: 30%; }

.table-col li.col4 { width: 15%; }

.table-col li.col5 { width: 25%; }

.rcpt { width: 700px; min-height: 500px; height: auto; margin-top: 5%; border: 1px solid #ccc; -webkit-border-radius: 10px; -moz-border-radius: 10px; border-radius: 10px; }

.rcpt .section { margin-top: 30px; }

.rcpt-cash { width: 381px; height: 840px; background-color: #fff; display: none; }

.rcpt-cash .pop-header { margin-top: 33px; }

.rcpt-cash .btn-cls { position: absolute; top: 26px; right: 18px; }

.rcpt-cash .pop-section { width: 351px; height: 627px; margin-top: 24px; font: 1.2rem "Dotum"; background: url("../images/bg-receipt-cash.png") no-repeat center center/cover; }

.rcpt-cash .btn-area { margin-top: 35px; }

.table-cash { margin: 10px; box-sizing: border-box; width: 331px; }

.table-cash tr td { vertical-align: middle; padding: 10px 3px 0 3px; box-sizing: border-box; }

.table-cash tr td.dp2 { padding: 0; }

.table-cash tr td.dp2 table tr td { padding: 10px 3px 0 3px; }

.pop-wrapper { padding: 15px; box-sizing: border-box; }

.pop-wrapper .t-tit { font-size: 2rem; line-height: 1; text-align: center; }

.btn-cls { width: 23px; height: 23px; }

.btn-cls a { display: block; width: 100%; height: 100%; font-size: 2.2rem; }

.btn-cls a::after { content: ""; display: inline-block; }

.line1 { display: block; width: 100%; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }
